export class SingleTimeoutManager {
  timeout: any = null;

  public set(cb, time) {
    if (this.timeout) clearTimeout(this.timeout);
    this.timeout = setTimeout(() => {
      this.timeout = null;
      cb && cb();
    }, time);
  }

  public setDelay(time) {
    return this.set(null, time);
  }

  public clear() {
    if (this.timeout) {
      clearTimeout(this.timeout);
      this.timeout = null;
    }
  }

  public active() {
    return this.timeout;
  }
}
